@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&family=Lexend+Deca&display=swap");
html {
  font-size: 100%;
}

/* Box sizing rules */
*,
*::before,
*::after {
  font-size: inherit;
  box-sizing: border-box;
}

/* Set base body defaults */
body {
  padding: 0;
  min-height: 100vh;
  line-height: 1.6;
  /* can be between 1.5 and 1.7 */
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

:root {
  --clr-primary-background: #090b1b;
  --clr-card-background: #1b1938;
  --clr-accent: #aa5cdb;
  --clr-heading-main: white;
  --clr-heading-stat: rgba(255, 255, 255, 0.6);
  --clr-body: rgba(255, 255, 255, 0.75);
  --ff-primary: "Inter", sans-serif;
  --ff-secondary: "Lexend Deca", sans-serif;
  --fw-normal: 400;
  --fw-bold: 700;
  --fs-base: 0.9rem;
  --lh: 1.6;
  --screen-sm: 375px;
  --screen-lg: 1440px;
}